// needs to be a separate file to lazy load it to workaround
// gatsby trying to aggressively render it not at runtime

export default function SentinelBeamModel() {
  return (
    <model-viewer
      src="/models/sentinel_beam.glb"
      shadow-intensity="5.4"
      shadow-softness="0.52"
      style={{width: "100vw", height: "100vh"}}
      camera-controls
    />
  )
}

